}

.footer-txt, .footer-link {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.19;
}

.footer-link--active:hover {
  color: orange;
}

.footer-link--animation {
  padding-bottom: 1px;
  text-decoration: none;
  position: relative;
}

.footer-link--animation:before {
  content: "";
  height: 1px;
  width: 100%;
  transform-origin: 0;
  background-color: #000;
  transition: transform .3s ease-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(1);
}

.footer-link--animation:hover:before {
  transform-origin: 100%;
  color: #000;
  background-color: #000;
  transform: scaleX(0);
}

:root {
